import React, {useEffect, useState} from "react";
import './index.css';
import {
    AdaptivityProvider,
    ConfigProvider,
    AppRoot,
    SplitLayout,
    SplitCol,
    View,
    Panel,
    PanelHeader,
    usePlatform,
    Input,
    Button,
    Spinner,
} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import {createRoot} from "react-dom/client";
import {requestGetIdeas, requestGroup} from "./requestAPI";
import {FilterPanel} from "./components/FilterPanel";
import {CardGroup} from "./components/CardGroup";

const Example = () => {
    const platform = usePlatform();

    const [loading, setLoading] = useState(false) // ()
    const [data, setData] = useState([]) // 
    // const [openFriends, setopenFriends] = useState(false) //    
    // const [changeId, seChangeId] = useState(-1) // id  

    //    
    const [filterClosed, setFilterClosed] = useState(undefined) //  
    const [haveColor, setHaveColor] = useState([]) //   
    const [changeColor, setChangeColor] = useState([]) //   
    const [baseSlipValue, setBaseSlipValue] = useState([0,0]) //    -   
    const [changeSlipValue, setChangeSlipValue] = useState([0,0]) //     - 
    const [onlyFriends, setOnlyFriends] = useState(false) //     - 

    const [sortType, setSortType] = useState(false) //     - 
    const [getURL, setGetURL] = useState("") //     - 


    useEffect(()=>{
        requestGroup(setLoading, setData)
    }, [])

    /**
     *   
     */
    useEffect(() =>{
        let newListColor = haveColor
        let newColorVal = []
        let minMembers = 1e9
        let maxMembers = 0
        for (let i = 0; i < data.length; i++){
            if (!newListColor.includes(data[i].avatar_color) && data[i].avatar_color !== undefined)//   -   
                newListColor.push(data[i].avatar_color)

            if (data[i].members_count > maxMembers)//   - 
                maxMembers = data[i].members_count

            if (data[i].members_count < minMembers)//   - 
                minMembers = data[i].members_count

        }
        for (let i = 0; i < newListColor.length; i++){ //  
            newColorVal.push({value:newListColor[i], label:newListColor[i]})
        }
        setHaveColor(newColorVal) //  

        setBaseSlipValue([minMembers, maxMembers])
        setChangeSlipValue([minMembers, maxMembers])
    }, [data])

    // /**
    //  *      
    //  * @param changeId . id 
    //  */
    // const funCheckFriend = (changeId) => {
    //     setopenFriends(true)
    //     seChangeId(changeId)
    // }
    // /**
    //  *   
    //  */
    // const funCloseFriendPanel = () => {
    //     setopenFriends(false)
    //     seChangeId(-1)
    // }

    /**
     *   
     * @param item 
     * @returns {false|boolean|*|boolean}     
     */
    function filterItems(item){
        let flag = false
        if (changeColor.length !== 0)
        {
            for (let i = 0; i < changeColor.length; i++)
                if (changeColor[i].value ===  item.avatar_color)
                {
                    flag = true
                    break
                }
        }
        else
            flag = true

        return  (filterClosed === undefined || item.closed === filterClosed) && flag
            && ((item.members_count >= changeSlipValue[0] && item.members_count <= changeSlipValue[1]) || item.members_count === undefined)
            && (onlyFriends? item.friends && item.friends.length > 0: true)
    }

    return (
        <AppRoot>
            <SplitLayout header={platform !== 'vkcom' && <PanelHeader delimiter="none" />}>
                <SplitCol autoSpaced>
                    <View activePanel="main">
                        <Panel id="main">
                            <PanelHeader className="MainHeader"> </PanelHeader>

                            <FilterPanel
                                changeColor={changeColor}
                                haveColor={haveColor}
                                setChangeColor={setChangeColor}
                                baseSlipValue={baseSlipValue}
                                changeSlipValue={changeSlipValue}
                                setFilterClosed={setFilterClosed}
                                setChangeSlipValue={setChangeSlipValue}
                                setOnlyFriends={setOnlyFriends}
                                setSortType={setSortType}/>

                            {loading && <Spinner size="large" style={{margin: '20px 0'}}/>}

                            <CardGroup data={data} filterid={filterItems}  sortType={sortType}/>
                            <Input
                                id="exampleClickable"
                                value={getURL}
                                onChange={e => setGetURL(e.target.value)}
                                type="text"
                                placeholder=" URl  get "
                            />
                            <Button
                                loading={loading}
                                mode="secondary"
                                onClick={() => {requestGetIdeas(setData, setLoading, getURL)}}
                            >
                                
                            </Button>
                        </Panel>
                    </View>
                </SplitCol>


                {/*{openFriends && <FriendsPanel data={data} changeId={changeId} funCloseFriendPanel={funCloseFriendPanel} openFriends={openFriends}/>}*/}

            </SplitLayout>
        </AppRoot>
    );
};

const container = document.getElementById('root');
const root = createRoot(container); // createRoot(container!) if you use TypeScript
root.render(
    <ConfigProvider>
        <AdaptivityProvider>
            <Example />
        </AdaptivityProvider>
    </ConfigProvider>,
);
